 .card {
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
        transition: 0.3s;
        width: 75%;
    }
      .img-ma{
        width:250px;
        height: 180px;
        justify-content: center;
      }
      .productsWrapper {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 30px;
    }

    .container{
        text-align: center;
    }
    .btn {
        border: none;
        outline: none;
        background: #764abc;
        padding: 5px 10px;
        color: #fff;
        border-radius: 5px;
        font-weight: bold;
        cursor: pointer;
        transition: all 0.3s ease-in-out;
    }
    
    .btn:hover {
        background: #513282;
    }
    .cardC{
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: #fff;
        margin-bottom: 20px;
        padding: 14px;
        border-radius: 5px;

    }
    .img-c{
        width:100px;
    }
    .head{
        color: #b08fe5;
    }
    .head:hover{
        color: #c97db3;
    }
    